body{
  background-image: url("/rewer/uploads/background.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  color: whitesmoke;
  font-family: Tahoma, "Geneva", sans-serif;
  min-height: 800px;
}


h1, h2{
  margin: 15px;
}
 
label {
  color: whitesmoke;
  margin: 5px;
}

input, textarea {
  color: white;
  padding: 5px;
  background: #777777; 
  margin: 10px;
  border-radius: 2px;
  outline-width: 1px;
  font-size: 18px;
  border-radius: 5px;
}

input:focus, textarea:focus{
  outline-width: 0;
}

select{
  color: white;
  padding: 5px;
  background: #000000; 
  background: -webkit-linear-gradient(to top, #434343, #000000);  
  background: linear-gradient(to top, #434343, #000000); 
  margin: 10px;
  border-radius: 2px;
  outline-width: 1px;
  font-size: 18px;
}

option{
  color: black;
}

button {
  background: #870000;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #190A05, #870000);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #190A05, #870000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: whitesmoke;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 5px;
  border-radius: 5px;
  outline-style: none;
}

button:hover {
  background: #e52d27;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #b31217, #e52d27);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #b31217, #e52d27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.save{
  background: #3a7bd5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3a6073, #3a7bd5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3a6073, #3a7bd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.save:hover{
  background: #2c3e50;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3498db, #2c3e50);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3498db, #2c3e50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.grid-container {
    display: grid;
    grid-template-columns: auto 60% auto;
    /*background-color: #2196F3; */
  }
  .grid-item {
    padding: 20px;
  }

.double-grid {
    display: grid;
    grid-template-columns: 50% 50%;;
}

::placeholder{
  color: rgb(240, 193, 193);
  font-style: italic;
}

.anchor-btn{
  background: #870000;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #190A05, #870000);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #190A05, #870000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: whitesmoke;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 5px;
  border-radius: 5px;
  outline-style: none;
  text-decoration: none;
  border-width: 1px;
  border-color: gray;
}

.anchor-btn:hover{
  background: #e52d27;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #b31217, #e52d27);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #b31217, #e52d27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.anchor-btn:active{
  background: #200122;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #6f0000, #200122);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #6f0000, #200122); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.main{
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.bottom-border{
  border-bottom-width: 1px;
  border-bottom: gray;
  border-bottom-style: solid;
  margin-bottom: 30px;
} 

.right-border{
  border-right-width: 1px;
  border-bottom: gray;
  border-right-style: solid;
}

.side-container{
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 10px;
  padding: 15px;
  margin-top: 50px;
}

.main-container{
  margin: auto;
  min-width: 60%;
  max-width: 800px;
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 10px;
  min-height: 800px;
}

.main-grid{
  display: grid;
  grid-template-columns: 25% 45% 30%;
}

.home-grid{
  display: grid;
  grid-template-columns: 65% 35%;
}

.main-item{
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
}

.follow-side{
  min-width: 250px;
  max-width: 350px;
}

.large-side{
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.medium-side{
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

.small-side{
  min-width: 50px;
  max-width: 150px;
  margin-left: auto;
  margin-right: auto;
}

.left-item{
  height: auto;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

.right-item{
  margin-left: auto;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

a {
  text-decoration: none;
  color: rgb(219, 0, 0);
}

.center-text{
  text-align: center;
  margin: auto;
  margin-bottom: 20px;
  margin-top: 10px;
}

a:hover{
  text-decoration: under;
  color: white;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

.main-title{
  margin: 20px;
}

.highlight{

}

.highlight:hover{
  background-color: rgba(255, 255, 255, 0.3);
}